.post-feed {
    position: relative;
    display: block;
    margin: 0 -20px;
    padding: 40px 0 0 0;

    .post-card {
        display: block;
        overflow: hidden;
        margin: 0 20px 40px;
        min-height: 300px;
        background: #fff center center;
        background-size: cover;
        border-radius: 5px;
        box-shadow: rgba(39,44,49,0.06) 8px 14px 38px, rgba(39, 44, 49, 0.03) 1px 3px 8px;
        transition: all 0.5s ease;
        
        &:hover {
            box-shadow: rgba(39,44,49,0.07) 8px 28px 50px, rgba(39, 44, 49, 0.04) 1px 6px 12px;
            transition: all 0.4s ease;
            transform: translate3D(0, -1px, 0) scale(1.02);
        }

        .post-card-image-link {
            position: relative;
            display: block;
            overflow: hidden;
            border-radius: 5px 5px 0 0;

            .post-card-image {
                width: 100%;
                background: var(--lightgrey) no-repeat center center;
                object-fit: contain;
            }
        }

        

        .post-card-tags {
            display: block;
            margin-bottom: 4px;
            color: var(--midgrey);
            font-size: 1.2rem;
            line-height: 1.15em;
            font-weight: 500;
            letter-spacing: 0.5px;
            text-transform: uppercase;
        }
    }

    .post-card-content {
        flex-grow: 1;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    
    
        .post-card-title {
            margin-top: 0;
        }
    
        .post-card-content-link {
            position: relative;
            flex-grow: 1;
            display: block;
            padding: 25px 25px 0;
            color: var(--darkgrey);
    
            &:hover {
                text-decoration: none;
            }

            .post-card-excerpt {
                font-family: Georgia, serif;
            }
        }
    
        .post-card-meta {
            display: flex;
            justify-content: space-between;
            align-items: flex-end;
            padding: 0 25px 25px;

            .profile-image-wrapper,
            .avatar-wrapper {
                position: relative;
            }
        }
    }
}

$avatar-bgcolor: #e3e9ed;

.author-profile-image,
.avatar-wrapper {
    display: block;
    width: 100%;
    height: 100%;
    background: $avatar-bgcolor;
    border-radius: 100%;

    object-fit: cover;
}

.author-list {
    display: flex;
    flex-wrap: wrap-reverse;
    margin: 0;
    padding: 0;
    list-style: none;
}

.author-list-item {
    position: relative;
    flex-shrink: 0;
    margin: 0;
    padding: 0;

    &:nth-child(1) {
        z-index: 10;
    }
    &:nth-child(2) {
        z-index: 9;
    }
    &:nth-child(3) {
        z-index: 8;
    }
    &:nth-child(4) {
        z-index: 7;
    }
    &:nth-child(5) {
        z-index: 6;
    }
    &:nth-child(6) {
        z-index: 5;
    }
    &:nth-child(7) {
        z-index: 4;
    }
    &:nth-child(8) {
        z-index: 3;
    }
    &:nth-child(9) {
        z-index: 2;
    }
    &:nth-child(10) {
        z-index: 1;
    }

    &:hover .author-name-tooltip {
        opacity: 1.0;
        transform: translateY(0px);
    }
}


.static-avatar {
    display: block;
    overflow: hidden;
    margin: 0 -5px;
    width: 34px;
    height: 34px;
    border: #fff 2px solid;
    border-radius: 100%;
}

.moving-avatar {
    display: block;
    overflow: hidden;
    margin: 0 -6px;
    width: 56px;
    height: 56px;
    border: #fff 2px solid;
    border-radius: 100%;
    transition: all 0.5s cubic-bezier(0.4, 0.01, 0.165, 0.99) 0.7s;
}

@media (min-width: 800px) {
    .author-list:hover .moving-avatar {
        margin: 0;
        transition: all 0.3s cubic-bezier(0.4, 0.01, 0.165, 0.99);
    }
}

.author-name-tooltip {
    position: absolute;
    bottom: 105%;
    z-index: 999;
    display: block;
    padding: 2px 8px;
    color: white;
    font-size: 1.2rem;
    letter-spacing: 0.2px;
    white-space: nowrap;
    background: var(--darkgrey);
    border-radius: 3px;
    box-shadow: rgba(39,44,49,0.08) 0 12px 26px, rgba(39, 44, 49, 0.03) 1px 3px 8px;
    opacity: 0;
    transition: all 0.3s cubic-bezier(0.4, 0.01, 0.165, 0.99);
    transform: translateY(6px);
    pointer-events: none;
}

@media (max-width: 650px) {
    .author-name-tooltip {
        display: none;
    }
}

.reading-time {
    flex-shrink: 0;
    margin-left: 20px;
    color: var(--midgrey);
    font-size: 1.2rem;
    line-height: 33px;
    font-weight: 500;
    letter-spacing: 0.5px;
    text-transform: uppercase;
}

/* Special Styling for home page grid (below):

The first post in the list is styled to be bigger than the others and take over
the full width of the grid to give it more emphasis. Wrapped in a media query to
make sure this only happens on large viewports / desktop-ish devices.

 */

@media (min-width: 795px) {

    .post-card{
        
        .post-card-image-link {
            border-radius: 5px 0 0 5px;
        }

        h2 {
            font-size: 2.6rem;
        }

        p {
            font-size: 1.8rem;
            line-height: 1.55em;
        }

        .post-card-content-link {
            padding: 30px 40px 0;
        }

        .post-card-meta {
            padding: 0 40px 30px;
        }
    }
}

.home-template .site-header:after {
    display: none;
}


/* Adjust some margins for smaller screens */
@media (max-width: 650px) {
    .post-feed {
        padding-top: 5vw;

        .post-card {
            margin: 0 20px 5vw;
        }
    }
}